@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&display=swap);
/*===== GOOGLE FONTS =====*/

/*===== VARIABLES CSS =====*/
:root{
  --header-height: 3rem;
  --font-semi: 600;
}

/*===== Colores =====*/
:root{
  --first-color: #4070F4;
  --second-color: #0E2431;
}

/*===== Fuente y tipografia =====*/
:root{
  --body-font: 'Poppins', sans-serif;
  --big-font-size: 2rem;
  --h2-font-size: 1.25rem;
  --normal-font-size: 0.938rem;
}
@media screen and (min-width: 768px){
  :root{
    --big-font-size: 3.5rem;
    --h2-font-size: 2rem;
    --normal-font-size: 1rem;
  }
}

/*===== Margenes =====*/
:root{
  --mb-1: 0.5rem;
  --mb-2: 1rem;
  --mb-3: 1.5rem;
  --mb-4: 2rem;
  --mb-5: 2.5rem;
  --mb-6: 3rem;
}

/*===== z index =====*/
:root{
  --z-back: -10;
  --z-normal: 1;
  --z-tooltip: 10;
  --z-fixed: 100;
}

/*===== BASE =====*/
*,::before,::after{
  box-sizing: border-box;
}
html{
  scroll-behavior: smooth;
}
body{
  margin: 3rem 0 0 0;
  margin: var(--header-height) 0 0 0;
  font-family: 'Poppins', sans-serif;
  font-family: var(--body-font);
  font-size: 0.938rem;
  font-size: var(--normal-font-size);
  color: #0E2431;
  color: var(--second-color);
}
h1,h2,p{
  margin: 0;
}
ul{
  margin: 0;
  padding: 0;
  list-style: none;
}
a{
  text-decoration: none;
}
img{
  max-width: 100%;
  height: auto;
  display: block;
}

/*===== CLASS CSS ===== */
.section-title{
  position: relative;
  font-size: 1.25rem;
  font-size: var(--h2-font-size);
  color: #4070F4;
  color: var(--first-color);
  margin-top: 1rem;
  margin-top: var(--mb-2);
  margin-bottom: 2rem;
  margin-bottom: var(--mb-4);
  text-align: center;
}
.section-title::after{
  position: absolute;
  content: "";
  width: 64px;
  height: 0.18rem;
  left: 0;
  right: 0;
  margin: auto;
  top: 2rem;
  background-color: #4070F4;
  background-color: var(--first-color);
}
.section{
  padding-top: 3rem;
  padding-bottom: 2rem;
}
.button{
  display: inline-block;
  background-color: #4070F4;
  background-color: var(--first-color);
  color: #fff;
  padding: .75rem 2.5rem;
  font-weight: 600;
  font-weight: var(--font-semi);
  border-radius: .5rem;
}
.button:hover{
  box-shadow: 0 10px 36px rgba(0,0,0,.15);
}

/*===== LAYOUT =====*/
.bd-grid{
  max-width: 1024px;
  display: grid;
  grid-template-columns: 100%;
  grid-column-gap: 2rem;
  width: calc(100% - 2rem);
  margin-left: 1rem;
  margin-left: var(--mb-2);
  margin-right: 1rem;
  margin-right: var(--mb-2);
}

/*=== Media Queries ===*/
@media screen and (min-width: 768px){
    body{
        margin: 0;
    }
    .section{
        padding-top: 4rem;
        padding-bottom: 3rem;
    }
    .section-title{
        margin-bottom: 3rem;
        margin-bottom: var(--mb-6);
    }
    .section-title::after{
        width: 80px;
        top: 3rem;
    }
}

@media screen and (min-width: 1024px){
    .bd-grid{
        margin-left: auto;
        margin-right: auto;
    }
}

/*===== LAYOUT =====*/
.l-header{
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: var(--z-fixed);
    background-color: #fff;
    box-shadow: 0 1px 4px rgba(146,161,176,.15);
}

/*===== NAV =====*/
.nav{
    height: var(--header-height);
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    font-weight: var(--font-semi);
}
@media screen and (max-width: 768px) {
    .nav__menu {
        position: fixed;
        top: var(--header-height);
        right: -100%;
        width: 80%;
        height: 100%;
        padding: 2rem;
        background-color: var(--second-color);
        transition: .5s;
    }
}
.nav__item{
    margin-bottom: var(--mb-4);
}
.nav__link{
    position: relative;
    color: #fff;
}
.nav__link:hover{
    position: relative;
}
.nav__link:hover::after{
    position: absolute;
    content: "";
    width: 100%;
    height: 0.18rem;
    left: 0;
    top: 2rem;
    background-color: var(--first-color);
}
.nav__logo{
    color: var(--second-color);
}
.nav__toggle{
    color: var(--second-color);
    font-size: 1.5rem;
    cursor: pointer;
}

/*Active menu*/
.active::after{
    position: absolute;
    content: "";
    width: 100%;
    height: 0.18rem;
    left: 0;
    top: 2rem;
    background-color: var(--first-color);
}
  
/*=== Show menu ===*/
.show{
    right: 0;
}

  /*=== Media Queries ===*/
@media screen and (min-width: 768px){
    .nav{
        height: calc(var(--header-height) + 1rem);
    }
    .nav__list{
        display: -webkit-flex;
        display: flex;
        padding-top: 0;
    }
    .nav__item{
        margin-left: var(--mb-6);
        margin-bottom: 0;
    }
    .nav__toggle{
        display: none;
    }
    .nav__link{
        color: var(--second-color);
    }
}
/*=== HOME ===*/
.home{
    height: calc(100vh - 3rem);
    grid-row-gap: 1rem;
    row-gap: 1rem;
}
.home__profile{
    -webkit-align-self: flex-end;
            align-self: flex-end;
    margin-top: var(--mb-6);
}
.home__social{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    position: absolute;
    right: 0;
    /* bottom: 0; */
    margin-right: var(--mb-2);
}
.home__social-icon{
    width: -webkit-max-content;
    width: max-content;
    margin-bottom: var(--mb-2);
    margin-left: var(--mb-2);
    font-size: 1.5rem;
    color: var(--second-color);
}
.home__social-icon:hover{
    color: var(--first-color);
}
.home__img{
    justify-self: center;
}
.home__data{
    -webkit-align-self: flex-start;
            align-self: flex-start;
    margin-bottom: var(--mb-6);
}
.home__title{
    font-size: var(--big-font-size);
    margin-bottom: var(--mb-5);
}
.home__title-color{
    color: var(--first-color);
}

/*=== Media Queries ===*/
@media screen and (min-width: 768px){
    .home{
        grid-template-columns: repeat(2,1fr);
        -webkit-align-items: center;
                align-items: center;
        height: 100vh;
    }
    .home__profile{
        -webkit-align-self: center;
                align-self: center;
    }
    .home__social{
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: row;
                flex-direction: row;
        position: inherit;
        margin-top: var(--mb-3);
        -webkit-justify-content: center;
                justify-content: center;
        margin-right: 0;
    }
    .home__social-icon{
        margin-bottom: 0;
        margin: 0 var(--mb-2);
    }
    .home__data{
        -webkit-align-self: center;
                align-self: center;
    }
}

@media screen and (min-width: 1024px){
    .home__img{
        right: 10%;
    }
}

.about__container{
    grid-row-gap: 2rem;
    row-gap: 2rem;
    text-align: center;
}
.about__subtitle{
    margin-bottom: var(--mb-2);
}
.about__img1{
    /* align-self: flex-start; */
    justify-self: center;
    margin-bottom: var(--mb-3);
}
.about__img1 img{
    width: 200px;
    /* border-radius: .5rem; */
}
.about__button{
    border: none;
    outline: none;
    font-size: var(--normal-font-size);
    cursor: pointer;
    margin-top: var(--mb-4);
    justify-items: center;
}

/*=== Media Queries ===*/
@media screen and (min-width: 768px){
    .about__container{
        grid-template-columns: repeat(2,1fr);
        -webkit-align-items: center;
                align-items: center;
    }
    .about__text{
        text-align: left;
        text-align: initial;
    }
    .about__img{
        justify-self: center;
    }
    .about__img img{
        width: 230px;
    }
    .about__img1{
        display: none;
    }
}


@media screen and (max-width: 768px) {
    .about__img{
        display: none;
    }
}
.skills__container{
    grid-row-gap: 2rem;
    row-gap: 2rem;
    text-align: center;
}
.skills__subtitle{
    margin-bottom: var(--mb-2);
}
.skills__text{
    margin-bottom: var(--mb-4);
}
.skills__data{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    position: relative;
    font-weight: var(--font-semi);
    padding: .5rem 1rem;
    margin-bottom: var(--mb-4);
    border-radius: .5rem;
    box-shadow: 0 4px 25px rgba(14,36,49,.15);
}
.skills__icon{
    font-size: 2rem;
    margin-right: var(--mb-2);
    color: var(--first-color);
}
.skills__names{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}
.skills__bar{
    position: absolute;
    left: 0;
    bottom: 0;
    background-color: var(--first-color);
    height: .25rem;
    border-radius: .5rem;
    z-index: var(--z-back);
}
.skills__jquery{
    width: 85%;
}
.skills__react{
    width: 85%;
}
.skills__redux{
    width: 75%;
}
.skills__nodejs{
    width: 80%;
}
.skills__spring-boot{
    width: 50%;
}
.skills__sql{
    width: 75%;
}
.skills__github{
    width: 75%;
}
.skills__aws{
    width: 20%;
}

/*=== Media Queries ===*/
@media screen and (min-width: 768px){
    .skills__container{
        grid-template-columns: repeat(2,1fr);
        -webkit-align-items: center;
                align-items: center;
    }
    .skills__frontend{
        -webkit-align-self: flex-start;
                align-self: flex-start;
    }
    .skills__backend{
        -webkit-align-self: flex-start;
                align-self: flex-start;
    }
    .skills__others{
        -webkit-align-self: flex-start;
                align-self: flex-start;
    }
}
.contact__input{
    width: 100%;
    font-size: var(--normal-font-size);
    font-weight: var(--font-semi);
    padding: 1rem;
    border-radius: .5rem;
    border: 1.5px solid var(--second-color);
    outline: none;
    margin-bottom: var(--mb-4);
}
.contact__button__container{
    text-align: right;
}
.contact__button{
    /* display: block; */
    border: none;
    outline: none;
    font-size: var(--normal-font-size);
    cursor: pointer;
    margin-right: var(--mb-1);
}
.contact__information{
    display: -webkit-flex;
    display: flex;
    margin-bottom: var(--mb-4);
}
.contact__icon{
    font-size: 2rem;
    color: var(--first-color);
    margin-right: .75rem;
}
.contact__title{
    font-size: var(--normal-font-size);
    font-weight: var(--font-semi);
    margin: 0;
}
.contact_subtitle{
    font-size: var(--normal-font-size);
}

/*=== Media Queries ===*/
@media screen and (min-width: 768px){
    .contact__form{
        width: 360px;
    }
    .contact__container{
        justify-items: center;
    }
    .contact__information{
        width: 360px;
    }
}
.footer{
    background-color: var(--second-color);
    color: #fff;
    text-align: center;
    font-weight: var(--font-semi);
    padding: 2rem 0;
}
.footer__title{
    font-size: 2rem;
    margin-bottom: var(--mb-4);
}
.footer__social{
    margin-bottom: var(--mb-4);
}
.footer__icon{
    font-size: 1.5rem;
    color: #fff;
    margin: 0 var(--mb-2);
}

